.hero {
	height: 250px;
	background-image: cover;
/*	background-position: center;
	background-repeat: no-repeat;*/
	position: relative;
	text-align: center;
	padding: 30px;
	background-image: url("./img/press-banner.jpg");
	margin-bottom: 30px;
	-webkit-box-shadow: 0 4px 2px -2px gray;
	        box-shadow: 0 4px 2px -2px gray;
}

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, h2 {
	color: white;
}

.hero h1 {
	font-size: 80px;
	font-family: 'UnifrakturCook', cursive;
	margin: 15px;
}

.hero h2 {
	font-size: 30px;
}


